'use strict';

// MODULES //

import tanf from './../../../../base/special/tanf';
import f32 from '@stdlib/number/float64/base/to-float32';


// VARIABLES //

var ONE = f32( 1.0 );


// MAIN //

/**
* Evaluates the cotangent of a single-precision floating-point number (in radians).
*
* @param {number} x - input value (in radians)
* @returns {number} cotangent
*
* @example
* var v = cotf( 0.0 );
* // returns Infinity
*
* @example
* var v = cotf( 3.141592653589793/2.0 );
* // returns ~0.0
*
* @example
* var v = cotf( -3.141592653589793/4.0 );
* // returns ~-1.0
*
* @example
* var v = cotf( 3.141592653589793/4.0 );
* // returns ~1.0
*
* @example
* var v = cotf( NaN );
* // returns NaN
*/
function cotf( x ) {
	return f32( ONE / tanf( f32( x ) ) );
}


// EXPORTS //

export default cotf;
