'use strict';

// MODULES //

import cosf from './../../../../base/special/cosf';
import f32 from '@stdlib/number/float64/base/to-float32';


// VARIABLES //

var ONE = f32( 1.0 );


// MAIN //

/**
* Computes the versed cosine of a single-precision floating-point number (in radians).
*
* @param {number} x - input value (in radians)
* @returns {number} versed cosine
*
* @example
* var v = vercosf( 0.0 );
* // returns 2.0
*
* @example
* var v = vercosf( 3.141592653589793/2.0 );
* // returns 1.0
*
* @example
* var v = vercosf( -3.141592653589793/6.0 );
* // returns ~1.8660
*
* @example
* var v = vercosf( NaN );
* // returns NaN
*/
function vercosf( x ) {
	return f32( ONE + cosf( x ) );
}


// EXPORTS //

export default vercosf;
