'use strict';

/**
* Simultaneously compute the sine and cosine of a single-precision floating-point number (in radians).
*
* @module @stdlib/math/base/special/sincosf
*
* @example
* import sincosf from '@stdlib/math/base/special/sincosf';
*
* var v = sincosf( 0.0 );
* // returns [ ~0.0, ~1.0 ]
*
* v = sincosf( 3.141592653589793/2.0 );
* // returns [ ~1.0, ~0.0 ]
*
* v = sincosf( -3.141592653589793/6.0 );
* // returns [ ~-0.5, ~0.866 ]
*
* v = sincosf( NaN );
* // returns [ NaN, NaN ]
*
* @example
* import Float32Array from '@stdlib/array/float32';
* import sincosf from '@stdlib/math/base/special/sincosf';
*
* var out = new Float32Array( 2 );
*
* var v = sincosf.assign( 0.0, out, 1, 0 );
* // returns <Float32Array>[ ~0.0, ~1.0 ]
*
* var bool = ( v === out );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;
