'use strict';

// MODULES //

import isnanf from './../../../../base/assert/is-nanf';
import truncf from './../../../../base/special/truncf';
import f32 from '@stdlib/number/float64/base/to-float32';


// VARIABLES //

var ZERO = f32( 0.0 );
var ONE = f32( 1.0 );


// MAIN //

/**
* Wraps a single-precision floating-point value to the half-open interval `[min,max)`.
*
* @param {number} v - input value
* @param {number} min - minimum value
* @param {number} max - maximum value
* @returns {number} wrapped value
*
* @example
* var v = wrapf( 3.14, 0.0, 5.0 );
* // returns ~3.14
*
* v = wrapf( -3.14, 0.0, 5.0 );
* // returns ~1.86
*
* v = wrapf( 10.0, 0.0, 5.0 );
* // returns 0.0
*
* v = wrapf( -0.0, 0.0, 5.0 );
* // returns 0.0
*
* v = wrapf( 0.0, -0.0, 5.0 );
* // returns 0.0
*
* v = wrapf( NaN, 0.0, 5.0 );
* // returns NaN
*
* v = wrapf( 0.0, NaN, 5.0 );
* // returns NaN
*
* v = wrapf( 3.14, 0.0, NaN );
* // returns NaN
*
* v = wrapf( 3.14, 5.0, 0.0 );
* // returns NaN
*/
function wrapf( v, min, max ) {
	var delta;

	v = f32( v );
	min = f32( min );
	max = f32( max );
	if (
		isnanf( v ) ||
		isnanf( min ) ||
		isnanf( max ) ||
		max <= min
	) {
		return NaN;
	}
	// Normalize +-0 to +0...
	if ( v === 0.0 ) {
		v = ZERO;
	}
	if ( min === 0.0 ) {
		min = ZERO;
	}
	if ( max === 0.0 ) {
		max = ZERO;
	}
	// Simple case where value is already within range...
	if ( min <= v && v < max ) {
		return v;
	}
	// Perform range reduction...
	delta = f32( max - min );
	if ( v < min ) {
		v = f32( v + f32( delta * f32( truncf( f32( f32(min-v) / delta ) ) + ONE ) ) );
	}
	return f32( min + f32( f32( v-min ) % delta ) );
}


// EXPORTS //

export default wrapf;
