'use strict';

// MODULES //

import f32 from '@stdlib/number/float64/base/to-float32';


// VARIABLES //

var ZERO = f32( 0.0 );


// MAIN //

/**
* Computes the absolute value of a single-precision floating-point number `x`.
*
* @param {number} x - input value
* @returns {number} absolute value
*
* @example
* var v = absf( -1.0 );
* // returns 1.0
*
* @example
* var v = absf( 2.0 );
* // returns 2.0
*
* @example
* var v = absf( 0.0 );
* // returns 0.0
*
* @example
* var v = absf( -0.0 );
* // returns -0.0
*
* @example
* var v = absf( NaN );
* // returns NaN
*/
function absf( x ) {
	x = f32( x );
	if ( x < ZERO ) {
		return f32( -x );
	}
	return x;
}


// EXPORTS //

export default absf;
