/* eslint-disable stdlib/capitalized-comments */

'use strict';

// MODULES //

import abs from './../../../base/special/abs';
import absf from './../../../base/special/absf';
import labs from './../../../base/special/labs';
import cabs from './../../../base/special/cabs';
import cabsf from './../../../base/special/cabsf';
import identityUint32 from '@stdlib/number/uint32/base/identity';
import identityUint16 from '@stdlib/number/uint16/base/identity';
import identityUint8 from '@stdlib/number/uint8/base/identity';


// MAIN //

var data = [
	// NOTE: the following **must** match the order in `./types.json`. The order should be according to likelihood of use (e.g., if `float64` arrays are more likely, then `float64` types/data should come before `uint8`).

	// float64
	abs,
	abs,

	// float32
	absf,
	abs,
	abs,

	// generic
	abs,

	// complex128
	cabs,
	cabs,

	// complex64
	cabsf,
	cabs,
	cabs,

	// int32
	labs,
	labs,
	labs,
	labs,

	// int16
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,

	// int8
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,
	labs,

	// uint32
	identityUint32,
	identityUint32,
	identityUint32,

	// uint16
	identityUint16,
	identityUint16,
	identityUint16,
	identityUint16,
	identityUint16,
	identityUint16,

	// uint8
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,

	// uint8c
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8,
	identityUint8
];


// EXPORTS //

export default data;
