'use strict';

// MODULES //

import cosf from './../../../../base/special/cosf';
import f32 from '@stdlib/number/float64/base/to-float32';


// VARIABLES //

var ONE = f32( 1.0 );


// MAIN //

/**
* Evaluates the secant of a single-precision floating-point number (in radians).
*
* @param {number} x - input value (in radians)
* @returns {number} secant
*
* @example
* var v = secf( 0.0 );
* // returns 1.0
*
* @example
* var v = secf( 3.1415927410125732 );
* // returns -1.0
*
* @example
* var v = secf( -3.1415927410125732/3.0 );
* // returns ~2.0
*
* @example
* var v = secf( 3.1415927410125732/3.0 );
* // returns ~2.0
*
* @example
* var v = secf( NaN );
* // returns NaN
*/
function secf( x ) {
	return f32( ONE / cosf( f32( x ) ) );
}


// EXPORTS //

export default secf;
