'use strict';

// MODULES //

import sinf from './../../../../base/special/sinf';
import f32 from '@stdlib/number/float64/base/to-float32';


// MAIN //

/**
* Evaluates the cosecant of a single-precision floating-point number (in radians).
*
* @param {number} x - input value (in radians)
* @returns {number} cosecant
*
* @example
* var v = cscf( 0.0 );
* // returns Infinity
*
* @example
* var v = cscf( 3.141592653589793/2.0 );
* // returns 1.0
*
* @example
* var v = cscf( -3.141592653589793/6.0 );
* // returns ~-2.0
*
* @example
* var v = cscf( 3.141592653589793/6.0 );
* // returns ~2.0
*
* @example
* var v = cscf( NaN );
* // returns NaN
*/
function cscf( x ) {
	return f32( f32( 1.0 ) / sinf( f32( x ) ) );
}


// EXPORTS //

export default cscf;
